.lastwrap {
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: end;

    .lastcontent {
        width: 1200px;
        height: calc(100vh - 79px);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .contact {
            width: 1200px;
            height: 360px;
            display: flex;
            justify-content: center;
            align-items: center;

            .fade-up {
                opacity: 0;
                transform: translateY(80px);
                transition: all 1s cubic-bezier(0.12, 1, 0.76, 1);
            }

            .fade-up.show {
                opacity: 1;
                transform: translateY(0);
            }

            img {
                width: 360px;

            }

            .lasttxtbox {
                margin-left: 50px;

                h2,
                h3,
                p {
                    margin: 0;
                }

                h2 {
                    font-size: 40px;
                    font-weight: bold;
                    font-family: 'PartialSansKR-Regular';
                    letter-spacing: 0.1em;
                }

                h3 {
                    font-size: 30px;
                    font-weight: bold;
                    margin: 6px 0 30px 0;
                }

                p {
                    font-size: 26px;
                }

                .lasttext {
                    margin-top: 20px;
                    font-size: 14px;
                    color: #666;
                }
            }
        }
    }
}