@charset "utf-8";

.startpage {
    width: 100%;
    height: calc(100vh - 120px);
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .nullbox {
        width: 100%;
    }

    .middletext {
        .animate1 {
            font-family: 'PartialSansKR-Regular';
            letter-spacing: 0.3em;
            color: #8EC7FF;
            font-size: 80px;
            text-align: center;
            margin: 0;

            span {
                display: inline-block;
                opacity: 0;
                transform: translateY(30px);
                animation: fadeUpOne 0.8s forwards;
            }
        }

        .animate2 {
            font-size: 20px;
            text-align: center;
            margin: 10px 0 0 0;

            opacity: 0;
            animation: fadeUp 1s ease-out 1s forwards;
        }
    }

    @keyframes fadeUpOne {
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }

    @keyframes fadeUp {
        0% {
            transform: translateY(40px);
            opacity: 0;
        }

        100% {
            transform: translateY(0);
            opacity: 1;
        }
    }

    .morewrap {
        margin: 0 auto;
        width: 100%;
        max-width: 1200px;
        height: 76px;

        .size {
            color: #666;
            text-align: center;
            font-size: 14px;
        }

        .arrow {
            width: 98%;
            height: 76px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-bottom: 2px solid #999;
            border-top: 1px solid #999;

            img {
                width: 20px;
                height: 20px;
            }

            p {
                font-size: 18px;
                color: #333;
            }
        }
    }
}