@charset "utf-8";

.profilewrap {
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
    height: 100vh;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: end;

    .profilecont {
        width: 1200px;
        height: calc(100vh - 79px);
        display: flex;
        align-items: center;
        justify-content: center;

        .aboutme {
            width: 1200px;
            height: 760px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            .fade-up {
                opacity: 0;
                transform: translateY(240px);
                transition: all 1s cubic-bezier(0.12, 1, 0.76, 1);
            }

            .fade-up.show {
                opacity: 1;
                transform: translateY(0);
            }

            .amup {
                width: 1160px;
                display: flex;
                justify-content: center;
                border-bottom: 1px solid #999;

                .cen {
                    display: flex;
                    align-items: center;
                    margin-bottom: 28px;
                    width: 550px;

                    img {
                        width: 260px;
                    }

                    .amlefttext {
                        margin-top: 104px;

                        h2,
                        p {
                            margin: 0;
                        }

                        h2 {
                            font-size: 30px;
                            margin-bottom: 10px;
                        }

                        p {
                            font-size: 18px;


                            span {
                                font-size: 16px;
                                color: #999;
                            }
                        }
                    }
                }
            }

            .amdown {
                display: flex;
                justify-content: center;
                align-content: center;
            }

            .amleft {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                width: 540px;

                h3,
                p {
                    margin: 0;
                }

                h3 {
                    font-size: 20px;
                    margin-bottom: 6px;
                }

                p {
                    font-size: 15px;
                    margin: 6px 0 3px 0;

                    span {
                        font-size: 14px;
                        color: #999;
                    }
                }
            }

            .amright {
                width: 560px;
                .amrighttext {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                }

                h3,
                p {
                    margin: 0;
                }

                h3 {
                    font-size: 20px;
                    margin-bottom: 6px;
                }

                p {
                    font-size: 15px;
                    margin: 6px 0 3px 0;

                    span {
                        font-size: 16px;
                        font-weight: 600;
                    }
                }
            }
        }
    }
}