@charset "utf-8";

.projectwrap {
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
    height: 100vh;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: end;


    .projectwrap2 {
        height: calc(100vh - 79px);
        display: flex;
        justify-content: center;
        align-items: center;

        .fade-up {
            opacity: 0;
            transform: translateY(100px);
            transition: all 1.5s cubic-bezier(0.22, 1, 0.36, 1);
        }

        .fade-up.show {
            opacity: 1;
            transform: translateY(0);
        }

        .pjdtwrap {
            width: 1200px;
            height: 400px;
            display: flex;
            justify-content: space-around;
            align-items: end;



            h6 {
                margin: 0;
                margin-bottom: 14px;
                font-size: 12px;
                color: #333;
                font-weight: 600;
            }

            .autoscrollbox1 {
                width: 680px;
                height: 400px;
                margin-right: 30px;
                overflow: hidden;
                box-shadow: 1px 2px 3px 1px #c7c7c7;

                img {
                    width: 100%;
                    transition: transform 5s linear;
                }

                &:hover img {
                    transform: translateY(calc(-100% + 550px));
                }

            }

            .autoscrollbox2 {
                width: 680px;
                height: 400px;
                margin-right: 30px;
                overflow: hidden;
                box-shadow: 1px 2px 3px 1px #c7c7c7;

                img {
                    width: 100%;
                    transition: transform 3s linear;
                }

                &:hover img {
                    transform: translateY(calc(-100% + 550px));
                }
            }

            .autoscrollbox3 {
                width: 680px;
                height: 400px;
                margin-right: 30px;
                overflow: hidden;
                box-shadow: 1px 2px 3px 1px #c7c7c7;

                img {
                    width: 100%;
                    transition: transform 6s linear;
                }

                &:hover img {
                    transform: translateY(calc(-100% + 550px));
                }
            }

            .autoscrollbox4 {
                width: 680px;
                height: 400px;
                margin-right: 30px;
                overflow: hidden;
                box-shadow: 1px 2px 3px 1px #c7c7c7;

                img {
                    width: 100%;
                    transition: transform 10s linear;
                }

                &:hover img {
                    transform: translateY(calc(-100% + 550px));
                }
            }

            .autoscrollbox5 {
                width: 680px;
                height: 400px;
                margin-right: 30px;
                overflow: hidden;
                box-shadow: 1px 2px 3px 1px #c7c7c7;

                img {
                    width: 100%;
                    transition: transform 7s linear;
                }

                &:hover img {
                    transform: translateY(calc(-100% + 550px));
                }
            }

            .pjdttext {

                h2,
                h5,
                p {
                    margin: 0;
                }

                h2 {
                    font-size: 38px;
                    font-weight: 600;
                }

                p {
                    font-size: 20px;
                }

                .toollist {
                    margin-top: 10px;
                    display: flex;

                    h5 {
                        font-size: 16px;
                        font-weight: 600;
                        color: #8EC7FF;
                        margin-right: 12px;
                    }
                }

                .pjdttext2 {
                    display: flex;
                    align-items: baseline;
                    margin-top: 12px;

                    p {
                        font-size: 15px;
                    }

                    .date {
                        font-size: 13px;
                        color: #666;
                        margin-left: 8px;
                    }

                }

                .ex {
                    font-size: 16px;
                    margin-top: 20px;
                    line-height: 130%;
                }

                .btnwrap {
                    width: 450px;
                    display: flex;
                    justify-content: space-between;
                    margin-top: 20px;

                    button {
                        width: 220px;
                        height: 46px;
                        border: 2px solid #bed8f3;
                        border-radius: 6px;
                        background-color: #fff;
                        cursor: pointer;

                        a {
                            font-size: 18px;
                            color: #8ab1db;
                            text-decoration: none;
                        }

                        :hover {
                            opacity: 0.6;
                        }
                    }
                }
            }
        }
    }
}