@charset "utf-8";

// 폰트
@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'PartialSansKR-Regular';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/PartialSansKR-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}


// font-style
.App {
  font-family: 'Pretendard-Regular';
  color: #1F1F1F;
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  .pfwrap {
    margin: 0 auto;

    .headerwrap {
      width: 100%;
      max-width: 1200px;
      height: 76px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      margin-bottom: 0;
      border-top: 2px solid #999;
      border-bottom: 1px solid #999;
      background-color: #fff;
      position: sticky;
      top: 0;
      z-index: 999;


      .headertext {
        width: 98%;
        display: flex;
        justify-content: space-between;

        .header_right {
          display: flex;
          width: 50%;
          justify-content: space-between;
        }

        h4 {
          font-size: 18px;
          font-weight: 600;
          margin: 0;
          cursor: pointer;
        }
      }

      .headertext h4.active {
        color: #8EC7FF;
        font-weight: bold;
      }
    }
  }
}